import { createApp } from 'vue'
import paymentMessage from "./paymentMessage.vue";

import router from '@/router'
import '@/assets/tailwind.css'
import 'flowbite'
import axios from 'axios';
import VueAxios from 'vue-axios';
import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";
axios.defaults.xsrfCookieName = 'csrftoken';
axios.defaults.xsrfHeaderName = "X-CSRFTOKEN";



const app = createApp(paymentMessage)

app.use(Toast);
app.use(VueAxios, axios)
app.use(router)
// Mount the component to a unique element in the HTML template
app.mount('#payment-message-app')
// createApp(PaymentMessage).mount('#payment-message-app');
